  defineComponent,
  useContext,
  onMounted,
  ref,
  computed,
  onUnmounted
} from '@nuxtjs/composition-api';
import { gsap } from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { arrayFactorys } from '@/utils';
import Header from '@/components/organisms/Header.vue';

export default defineComponent({
  components: {
    Header
  },
  setup() {
    const { app } = useContext();

    const debugState = ref<string[]>([]);
    debugState.value = arrayFactorys(
      'https://source.unsplash.com/VkwRmha1_tI/800x533',
      5
    );
    const date = computed(() => {
      return app.$stores.home.snapList;
    });
    console.log(`top template${JSON.stringify(date)}`);

    // @see https://8oo.jp/blog/39/
    // ä¸ãè¦ã¦è²ããã
    // onMountedã§ãã©ã¦ã¶ããã¯ã«ãå¯¾å¿ãã§ããã
    onMounted(() => {
      if (process.client) {
        gsap.registerPlugin(ScrollTrigger);

        let sections = gsap.utils.toArray('.panel');
        console.log(sections);
        // GSAPã§ã¯ãtransform : translateXãtransform : translateYã®ä»£ããã«ãXåº§æ¨ï¼xï¼ãYåº§æ¨ï¼yï¼ãXãã¼ã»ã³ãï¼xPercentï¼ãYãã¼ã»ã³ãï¼yPercentï¼ãæä¾ãã¦ãã¾ãã
        /** topãWindowã¯ã°ã«ãªãããããããwindowå¹ã«ãã */

        gsap.to(sections, {
          xPercent: -100 * (sections.length - 1),
          ease: 'none',
          scrollTrigger: {
            trigger: '.container',
            pin: true,
            scrub: 1,
            snap: 1 / (sections.length - 1),
            // base vertical scrolling on how wide the container is so it feels more natural.
            end: '+=3500'
          }
        });
      }
    });

    onUnmounted(() => {
      gsap.registerPlugin();
    });

    return {
      debugState
    };
  }
});
